<template>
    <div class="main">
        <div class="topBg">
            <div class="topNav">
                <img class="logo" src="/static/images/utils/logo1_1.png" />
                <div @click="toDatav" class="datavLink">DATAV</div>
            </div>
            <div class="top">
                <div class="mt1"></div>
                <div class="mainTit">iCenter AI数据中心平台</div>
                <!-- <img src="/static/images/utils/vmware-logo-grey.svg" class="locLogo" /> -->
                <div class="copyRights">Copyright © 2023 清华大学基础工业训练中心iCenter. All Rights Reserved.</div>
                <div class="mt1"></div>
            </div>
        </div>

        <div>
            <div class="loginForm">
                <el-form ref="form" :model="loginForm" label-width="90px">
                    <el-form-item :label="$t('login.userName')">
                        <el-input class="logInt" v-model="loginForm.name" :placeholder="$t('login.userName')" size="big" type="text"></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('login.password')">
                        <el-input class="logInt" v-model="loginForm.password" :placeholder="$t('login.password')" size="big" type="password" show-password @keyup.enter.native="login()"></el-input>
                    </el-form-item>
                </el-form>
                <el-button @click="login" type="primary" class="loginBtn">{{$t('login.btn')}}</el-button>
                <div class="registerRow" @click="toReg">{{$t('login.register')}}</div>
            </div>
            <!-- <el-button type="primary" round style="display: block; margin: auto; margin-top: 50px;" @click="toIndex">SIGN IN</el-button> -->
        </div>

        <el-dialog :title="'Register'" width="560px" :close-on-click-modal="false" :close-on-press-escape="false" :visible.sync="userDialogVisible">
            <el-form ref="userForm" :model="userForm" label-width="0px" size="mini" :rules="userRules">
                <!-- <div class="colBlockTit">User Register</div> -->
                <el-row :gutter="20">
                    <el-col :span="24">
                        <div class="colFmTie">{{$t('login.userName')}}<span class="requiredDot">*</span></div>
                        <el-select v-model="userForm.group" size="mini" class="ufSel">
                            <el-option v-for="item in groupOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="24">
                        <div class="colFmTie">{{$t('login.userName')}}<span class="requiredDot">*</span></div>
                        <el-form-item label="" prop="name">
                            <el-input v-model="userForm.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <div class="colFmTie">{{$t('login.password')}}<span class="requiredDot">*</span></div>
                        <el-form-item label="" prop="password">
                            <el-input v-model="userForm.password" show-password></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialogFooter">
                <el-button size="mini" type="primary" @click="execReg">{{$t('login.register')}}</el-button>
                <el-button size="mini" @click="userDialogVisible = false">{{$t('button.cancel')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "login",
    data() {
        return {
            userGroupList: [],
            loginForm: {
                name: '',
                password: '',
            },
            userDialogVisible: false,
            userForm: {},
            groupOptions: [
                {
                    label: 'teacher',
                    value: 'teacher',
                },
                {
                    label: 'student',
                    value: 'student',
                },
            ],
            userRules: {
                "name": [{ required: true, message: '用户名必填', trigger: 'blur' }],
                "password": [{ required: true, message: '密码必填', trigger: 'blur' }],
                // "phone": [{ required: true, message: '手机号码必填', trigger: 'blur' }],
                // "IMAGE_W": [{ required: true, pattern: /^-?[1-9]\d*$/, message: 'Intger Required', trigger: 'blur' }],

            },
        };
    },
    methods: {
        loadUserGroup: function(){
            let _this = this;
            _this.req('/user_group', {}, function (data) {
                if (data && data.success) {
                    _this.userGroupList = data.obj.records;
                } else {
                    _this.$alert(data.message, _this.$t('tip.tip'), {
                        confirmButtonText: _this.$t('button.close'),
                        type: 'warning'
                    }).then(() => {
                    }).catch(() => { });
                }
            },'get', false);
        },
        login: function () {
            let _this = this;
            // hayo
            _this.initUserData({name:'Student.Lee', user_group_id: '3', token: 'xxxxxx'});
            // _this.initUserData({name:'Teacher.Wang', user_group_id: '2', token: 'xxxxxx'});
            // _this.initUserData({name:'admin', user_group_id: '1', token: 'xxxxxx'});
            return;
            _this.req('/auth', this.loginForm, function (data) {
                if (data && data.success) {

                    _this.initUserData(data.obj);
                } else {
                    _this.$alert(data.message, _this.$t('tip.tip'), {
                        confirmButtonText: _this.$t('button.close'),
                        type: 'warning'
                    }).then(() => {
                    }).catch(() => { });
                }
            });
        },
        initUserData: function (data) {
            let _this = this;
            let navMap = _this.m.getData(data.user_group_id).navMap;
            let menuArr = [];
            let funArr = [];
            for (let x in navMap) {
                let m1 = navMap[x];
                if (m1.type == 0) {
                    menuArr.push(m1);
                } else {
                    funArr.push(m1);
                }

                for (let y in m1.childMap) {
                    let m2 = m1.childMap[y];
                    if (m2.type == 0) {
                        m1.childList.push(m2);
                    } else {
                        funArr.push(m2);
                    }

                    for (let z in m2.childMap) {
                        let m3 = m2.childMap[z];
                        if (m3.type == 0) {
                            _this.menuSort(m2.childList, m3);
                            // m2.childList.push(m3);
                        } else {
                            funArr.push(m3);
                        }

                        // 第四层，基本上都是功能
                        for (let z1 in m3.childMap) {
                            let m4 = m3.childMap[z1];
                            if (m4.type == 1) {
                                funArr.push(m4);
                            }
                        }
                    }
                    m2.childMap = {};
                }
                m1.childMap = {};
            }

            sessionStorage.setItem("userMenuArr", JSON.stringify(menuArr));
            sessionStorage.setItem("userFunArr", JSON.stringify(funArr));
            sessionStorage.setItem("loginUser", data.name);
            sessionStorage.setItem("user_group_id", data.user_group_id);

            sessionStorage.setItem("ops-token", data.token);
            this.$router.push("/");
        },
        menuSort: function (list, m) {
            if (m.idx == undefined) {
                m.idx = 99999;
            }
            if (list.length == 0) {
                list.push(m);
            } else {
                for (let i in list) {
                    if (list[i].idx > m.idx) {
                        list.splice(i, 0, m);
                        return true;
                    }
                    if (i == list.length - 1) {
                        list.push(m);
                    }
                }
            }
        },
        resize: function () {
            let h = document.body.clientHeight;
            let mt = (h - 550) / 2 - 30;
            if (mt < 0) {
                mt = 0;
            }
            this.mt = mt;
        },
        toReg: function(){
            this.userDialogVisible = true;
        },
        execReg: function(){
            let _this = this;
            this.$refs['userForm'].validate(valid => {
                if (valid) {
                    this.userForm.user_group_id = 3;
                    _this.req('/user', this.userForm, function (data) {
                        if (data && data.success) {
                            _this.userDialogVisible = false;
                            _this.$message.success('Success');
                        } else {
                            _this.$alert(data.message, _this.$t('tip.tip'), {
                                confirmButtonText: _this.$t('button.close'),
                                type: 'warning'
                            }).then(() => {
                            }).catch(() => { });
                        }
                    });
                } else {
                    _this.$message.error("校验失败，请认真填写");
                }
            });
        },
        toDatav: function(){
            this.$router.push("/datav");
        },
    },
    mounted: function () {
        this.$nextTick(function () {
            console.log("mounted -- >");
            let _this = this;
            this.resize();
            window.addEventListener('resize', () => {
                _this.resize();
            });

            // this.loadUserGroup();
        });
    }
};
</script>

<style scoped>
.main {
    height: 100%;
}
.topBg {
    height: 50%;
}
.topNav{
    height: 80px;
    background-image: linear-gradient(to right, #6510ad,  #0084c9);
    /* background-image: linear-gradient(to left,#6fbe2e 19%,#00bbd6 43%,#0f61a9 57%,#5a39a0 81%); */
}
.logo{
    display: block;
    height: 58px;
    /* width: 180px; */
    width: 465px;
    margin-top: 11px;
    margin-left: 30px;
    float: left;
}
.datavLink{
    width: auto;
    float: right;
    line-height: 80px;
    margin-right: 30px;
    color: #fff;
    cursor: pointer;
}
.top {
    height: calc(100% - 80px);
    background-image: url('/static/images/utils/loginBg.webp');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    /* background-color: #6510AD; */
    /* background-image: linear-gradient(to bottom, rgba(101,16,173,0.8), rgba(101,16,173,0.5),  rgba(255,255,255,1)); */
    text-align: center;
    /* color: #6510ad; */
    overflow: hidden;
}
.mt1 {
    height: 20%;
}
.mainTit {
    font-size: 48px;
    width: 990px;
    margin: auto;
    margin-bottom: 40px;
    font-weight: 600;

    background-image: linear-gradient(45deg,#6510ad,#0084c9);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
    -webkit-animation:hue6sinfinitelinear;

}
.copyRights {
    font-size: 14px;
    font-weight: 300;
    color: #717074;
}
.mt1 {
    height: 15%;
}
.locLogo {
    margin: auto;
    width: 150px;
    /* height: 80px; */
    display: block;
    margin-bottom: 20px;
}

.loginForm {
    width: 350px;
    margin: auto;
    padding-top: 50px;
}

.loginBtn {
    width: 100%;
    box-sizing: border-box;
    border-radius: 20px;
    margin-top: 20px;
}

.logInt >>> input:-internal-autofill-selected {
    transition: background-color 5000s ease-in-out 0s;
    background-color: rgba(255, 255, 255, 0) !important;
}

.logInt >>> input:-webkit-autofill {
    transition: background-color 5000s ease-in-out 0s;
    background-color: rgba(255, 255, 255, 0) !important;
}

.logInt >>> input:-webkit-autofill:focus {
    transition: background-color 5000s ease-in-out 0s;
    background-color: rgba(255, 255, 255, 0) !important;
}

.registerRow{
    text-align: center;
    margin-top: 20px;
    border: 1px solid #fff;
    height: 40px;
    line-height: 38px;
    box-sizing: border-box;
    border-radius: 19px;
    font-size: 14px;
    cursor: pointer;
    color: #888;
}
.registerRow:hover{
    border-color: #dcdfe6;
    color: #666;
}

.ufSel{
    margin-bottom: 18px;
    width: 100%;
}
</style>



