// 暂未使用
const isNullOrEmpty = function(val) {
    if (val == null || val == "" || typeof val == undefined) {
        return true;
    } else {
        return false;
    }
};

const toCap = function(val) {
    if (isNaN(val)) {
        return "-";
    }

    let g = (val / (1024 * 1024 * 1024)).toFixed(1);
    let t = (val / (1024 * 1024 * 1024 * 1024)).toFixed(1);

    if (g < 1024) {
        return g + " GiB";
    } else {
        return t + " TiB";
    }
};

const toPer = function(val) {
    if (isNaN(val)) {
        return "-";
    }

    return (val * 100).toFixed(1);
};

export { isNullOrEmpty, toCap, toPer };



