<template>
    <el-container class="mainPage">
        <el-aside :width="isCollapse?'64px':'200px'" v-if="userGroupId=='1'">
            <!-- <div class="logoBlock">
                <img src="/static/images/utils/logo2.png" class="logo1" />
                <div class="logoSplit"></div>
                <img src="/static/images/utils/deepm2Logo2.png" class="deepm2Logo" />
            </div> --> <!-- 3bb4d4 8c335a -->
            <el-scrollbar style="height: calc(100% - 0px); z-index: 10; margin-top: 0px; background-image: linear-gradient(to bottom, #6510ad, #2c335a);"><!-- background-image: linear-gradient(to bottom, #6510ad, #2c335a); -->
                <el-menu class="" @open="handleOpen" @close="handleClose" :unique-opened="true" :collapse="isCollapse">
                    <template v-for="(item) in menuList" :index='item.id.toString()'>
                        <el-menu-item v-if="item.url!=''" :key="item.id" :index='item.id.toString()' @click="addTab(item.url, item.name)" class="selectNone">
                            <div class="dots">
                                <i :class="'iconfont'" v-html="item.icon?item.icon:'&#xe7cc;'"></i>
                                <span :title="item.name">{{item.name}}</span>
                            </div>
                        </el-menu-item>
                        <el-submenu v-else :key="item.id" :index="item.id.toString()" class="selectNone">
                            <template slot="title">
                                <div class="dots">
                                    <i :class="'iconfont'" v-html="item.icon?item.icon:'&#xe7cc;'"></i>
                                    <span :title="item.name">{{item.name}}</span>
                                </div>
                            </template>

                            <template v-for="(child) in item.childList" :index='child.id.toString()'>
                                <el-menu-item :key="child.id" :index='child.id.toString()' @click="addTab(child.url, child.name)" class="selectNone menuLev3">{{child.name}}</el-menu-item>
                            </template>
                        </el-submenu>
                    </template>
                </el-menu>
            </el-scrollbar>
            <div class="colpBtn" @click="toggleCollapse" :style="'left:'+(isCollapse?'39px':'175px')+'; top:'+colpTop+'px;'">
                <i :class="isCollapse?'el-icon-arrow-right':'el-icon-arrow-left'"></i>
            </div>
        </el-aside>
        <el-container class="rightContainer">
            <el-header>
                <div class="midBlock" :style="'padding-left:'+(isCollapse?'576px':'440px')">
                    <img class="logoBlock2" src="/static/images/utils/logo1_1.png" >
                    <div :class="'menuTopItem '+ (item.sel?'mtSel':'')" v-for="(item,index) in menuAll" :key="index" :index='item.id.toString()' @click="showMenu2(index)">{{item.name}}</div>


                    <el-dropdown @command="rightDropdown">
                        <span class="el-dropdown-link">
                            {{userNo}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <!-- <el-dropdown-item command="sysoper">系统操作</el-dropdown-item> -->
                            <el-dropdown-item command="logout">{{$t('login.logout')}}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    
                    <div v-if="helpTit" class="helpLink" @click="openUrl(helpUrl)">{{helpTit}}</div>
                </div>
            </el-header>
            <el-main>
                <el-row style="height: 100%;">
                    <el-col :span="24" style="height: 100%;">
                        <el-tabs id="mainTabs" v-model="currTabName" type="card" @tab-remove="removeTab" @tab-click="clickTab" style="height: 100%;">
                            <el-tab-pane v-for="(item) in tabArr" :key="item.name" :label="item.title" :name="item.name" :closable="item.closable">
                                <span slot="label">
                                    <span class="span-box">
                                        <i :class="'iconfont topBarIcon'" v-html="item.icon?item.icon:'&#xe7cc;'"></i>
                                        <span>{{item.title }}</span>
                                    </span>
                                </span>

                                <keep-alive>
                                    <tab-component :is="item.content" :key="item.ts" @addTab="addTab" @closeTab="closeTab" :curr="currTabName==item.name" :param="item.param"></tab-component>
                                </keep-alive>
                            </el-tab-pane>
                        </el-tabs>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>

    </el-container>
</template>

<script>
export default {
    name: "index",
    components: {},
    data() {
        return {
            circleUrl: "",
            menuAll: [],
            funAll: [],
            menuTopIndex: 0, // 选中的顶部菜单
            menuList: [],
            currTabName: 'dashboard',
            tabArr: [],
            mtWidth: 0,
            userNo: '',
            searchTxt: '',
            table: {
                size: 10,
                current: 1,
                pages: 0,
                total: 0,
                records: [],
            },
            loading: false,
            colpTop: 300,

            userGroupId: 1,
            isCollapse: true,

            helpUrl: '',
            helpTit: '',

        };
    },
    methods: {
        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        },
        showMenu2(index) {
            if(this.menuAll.length>index){
                let menuList = this.menuAll[index].childList;
                this.menuSort(menuList);
    
                this.menuList = menuList;
    
                this.$set(this.menuAll[this.menuTopIndex], "sel", false);
                this.$set(this.menuAll[index], "sel", true);
            }
            this.menuTopIndex = index;
        },
        menuSort: function (list) {
            list.sort(this.compare('idx'));
            if (list && list.length > 0) {
                for (let a in list) {
                    if (list[a].childList && list[a].childList.length > 0) {
                        this.menuSort(list[a].childList);
                    }
                }
            }
        },
        compare: function (p) {
            return function (a, b) {
                return a[p] - b[p];
            }
        },
        addTab(path, title, param, self) {
            if (!path) {
                this.$message({
                    showClose: true,
                    message: this.$t('menu.unConfig'),
                    type: 'warning'
                })
                return;
            }
            if (self == undefined) {
                self = false;
            }

            if (path.indexOf("?") != -1) {
                if (!param) {
                    param = {};
                }
                // TODO  解析参数
                let n = path.indexOf("?");
                let paramStr = path.substr(n + 1);
                let paramArr = paramStr.split("&");
                paramArr.forEach(function (nn, i) {
                    let arr = nn.split("=");
                    param[arr[0]] = arr[1];
                });

                path = path.substring(0, n);
            }


            let name = path.replaceAll('/', '');
            let pagePath = path;

            var comp = () => import('@/components' + pagePath + '.vue');
            if (!param) {
                param = {};
            }
            param.tabName = name;

            // TODO 拦截器此处进行判断权限

            // 是否已经打过该标签
            let has = false;
            let _this = this;
            this.tabArr.forEach((tab, index) => {
                if (tab.name === name || (self && _this.currTabName === tab.name)) {
                    has = true;
                    if (self) {
                        _this.$set(_this.tabArr[index], "param", param);
                        _this.$set(_this.tabArr[index], "content", comp);
                        _this.$set(_this.tabArr[index], "ts", new Date().getTime());
                    } else {
                        // 组件的key元素，如果变化，组件会从新渲染
                        _this.$set(_this.tabArr[index], "title", title);
                        _this.$set(_this.tabArr[index], "param", param);
                        _this.$set(_this.tabArr[index], "content", comp);
                        _this.$set(_this.tabArr[index], "ts", new Date().getTime());
                    }
                }
            });

            if (!has) {
                let icon = '';
                this.menuAll.forEach(function (n, i) {
                    n.childList.forEach(function (nn, ii) {
                        if (nn.url == path) {
                            icon = nn.icon;
                        }
                    });
                });

                if (!icon) {
                    // console.log('>>>>>>>>>>>> config icon');
                    let iconObj = {
                        "/dashboard": "&#xe751;",
                        "/monitor/monitorIsilon": "&#xe69e;",
                    };
                    if (iconObj[path]) {
                        icon = iconObj[path];
                    }
                }


                this.tabArr.push({
                    title: title,
                    name: name,
                    icon: icon,
                    param: param,
                    content: comp,
                    closable: path == '/dashboard' ? false : true
                });
                // console.log(name + '----' + icon);
            }
            if (!self) {
                this.currTabName = name;
            }
        },
        removeTab(targetName) {
            let tabs = this.tabArr;
            let active = this.currTabName;
            if (active === targetName) {
                tabs.forEach((tab, index) => {
                    if (tab.name === targetName) {
                        let nextTab = tabs[index + 1] || tabs[index - 1];
                        if (nextTab) {
                            active = nextTab.name;
                        }
                    }
                });
            }

            this.currTabName = active;
            this.tabArr = tabs.filter(tab => tab.name !== targetName);
        },
        clickTab: function (e) {
            // console.log(e);
        },
        closeTab: function (path) {
            this.removeTab(path.replaceAll('/', ''));
        },

        rightDropdown: function (command) {
            if (command == 'logout') {
                sessionStorage.removeItem("user-token");
                sessionStorage.removeItem("ops-token");
                sessionStorage.removeItem("csrf_access_token");

                this.req('/auth', {}, function (data) {
                }, 'delete');

                this.$router.push("/datav");
            }else if(command == 'sysoper'){
                this.sysoperVisible = true;
            }
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        imaHandleClick(tab, event) {
            console.log(tab, event);
        },

        toggleCollapse: function () {
            this.isCollapse = !this.isCollapse;

            setTimeout(function () {
                var myEvent = new Event('resize');
                window.dispatchEvent(myEvent);
            }, 100);
        },

        resize: function () {
            let h = document.body.clientHeight;
            let w = document.body.clientWidth;
            let mt = h - 300;
            if (mt < 0) {
                mt = 0;
            } else if (mt > 600) {
                mt = 600;
            }
            this.chatHeight = mt;
            // this.
            let colpTop = (h - 60 - 50) / 2 + 60;
            this.colpTop = colpTop;

            let warnLen = w - 1350;
            this.warnLen = warnLen;
        },
        openUrl: function(url){
            if(url){
                window.open(url);
            }
        }
    },
    mounted: function () {

        this.$nextTick(function () {
            let _this = this;

            this.userGroupId = sessionStorage.getItem("user_group_id");

            let menuAll = JSON.parse(sessionStorage.getItem("userMenuArr"));

            // console.log(JSON.stringify(menuAll));
            this.menuAll = menuAll;
            this.mtWidth = menuAll.length * 140;

            funAll = JSON.parse(sessionStorage.getItem("userFunArr"));
            this.userNo = sessionStorage.getItem("loginUser");
            this.showMenu2(0);

            this.addTab('/dashboard', this.$t('tabName.dashboard'));

            this.helpUrl = config.helpUrl;
            this.helpTit = config.helpTit;

            // 
            this.resize();
            window.addEventListener('resize', () => {
                this.resize();
            });
        });
    },
    beforeDestroy: function () {
    },
};
</script>
